<template>
  <div class="style">
    <top></top>
    <div class="addSort">
      <a href="javascript:void (0)" @click="add">
        <Icon type="android-add-circle"></Icon>
        <span>添加商品分类</span>
      </a>
    </div>
    <ul class="list">
      <li v-for="(item,key) in list">
        <div style="display: flex;align-items: center">
          <!--<Icon type="navicon" size="20" style="margin: 0 0.15rem;"></Icon>-->
          <span style="display: flex;">
<!--            <Icon type="navicon" size="20" style="margin: 0 0.15rem;position: relative;top: 0.02rem;"></Icon>-->
            <span v-if="!item.show">{{item.name}}</span>
            <span class="iconfont icon-bianji" style="margin:0 0.15em;cursor: pointer;color: #59A2ED;" v-if="!item.show" @click="editor(key)"></span>
            <input type="text" v-model="item.name" @blur="change(key,item)" v-if="item.show" @focus="textF(key)">
            <span class="iconfont icon-duihao in" style="margin-left: -0.25rem" v-if="item.show"></span>
        </span>
         <p class="sorts_p" style="color:#e64751;margin-left: 0.4rem" v-if="!item.name">商品分类名称只能包含字母、数字、空格和横线。</p>
        </div>

        <div>
          <strong style="color: #9F9F9F;" v-if="item.cate_number >= 1">{{item.cate_number}}件</strong>
          <strong style="color: #9F9F9F;"  v-else >没有商品</strong>
          <i class="iconfont icon-lajitong" style="margin: 0 0.2rem;cursor: pointer" @click="deleted(key,item)"></i>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import top from '../../top.vue'
  export default{
    data(){
      return{
        showList:false,
        show:true,
        name:'',
        list:[],
//        标识
        sort:0,
      }
    },
    methods:{
      textF(key){
        event.target.style.borderColor='skyblue';
      },
      add(){
        this.list.unshift({name:"",show:true,flag:false});
      },
      change(key,item){
        let Reg=/[A-Za-z0-9_\-\u4e00-\u9fa5]+/;
        if (this.list[key].name && Reg.test(this.list[key].name)){
          this.list[key].show=false;
          this.list.splice(key,1,this.list[key]);
          event.target.style.borderColor='gainsboro';
        }else {
          this.list[key].name='';
          event.target.style.borderColor='#e64751';
        }
          if(this.list[key].flag){
//              修改
            if(this.list[key].name){
              this.post("custom/Shop/updateCateName",{
                appid:this.$route.params.appid,
                name:this.list[key].name,
                cate_id:item.id
              }).then(res=>{
                if(res.data.code===10000){
                    this.$Modal.success({
                      title: "修改成功",
                      content: "成功修改商品分类"
                    });
                }
              })
            }
          }else{
//              创建
            if(this.list[key].name){
              this.post("custom/Shop/createCate",{
                appid:this.$route.params.appid,
                name:this.list[key].name
              }).then(res=>{
                if(res.data.code===10000){
                  this.$Modal.success({
                      title: "创建成功",
                      content: "创建商品分类成功"
                  });
                }
              })
            }
          }
      },
      editor(key){
        this.list[key].show=true;
        this.list[key].flag=true;
        this.list.splice(key,1,this.list[key])
      },
      deleted(key,item){
        this.post("custom/Shop/delCateById",{
          appid:this.$route.params.appid,
          cate_id:item.id
        }).then(res=>{
          if(res.data.code===10000){
            this.$Modal.success({
                title: "删除成功",
                content: "删除商品分类成功"
              });
            this.list.splice(key,1);
          }
        })
      }
    },
    mounted(){
//        获取分类列表
      this.post('custom/Shop/getCateList',{
          appid:this.$route.params.appid,
    }).then(res=>{
        if(res.data.code===10000){
          console.log(res.data)
              this.list=res.data.data.info;
              this.list.forEach(item=>{
                  item["show"]=false;
                 return item["flag"]=false;
              });
            window.frames[0].postMessage(this.list,"*");
          }
        })
    },
    components:{
      top
    }
  }
</script>
<style scoped>
.addSort{padding: 0.2rem 0}
input{
  display: inline-block;
  width:3rem;  
  border: 1px solid #CCCCCC;  
  padding: 0.08rem 0.1rem;
}
.addSort>a{ color: white;  display: inline-block;  padding: 0.11rem 0.3rem;  border-radius: 0.05rem;  background-color: #93b719;}
  .list>li:nth-child(1){
    border-top: 1px solid #E1E1E1;
  }
.list>li{
  padding: 0.1rem 0;
  border-bottom: 1px solid #E1E1E1;
}
.list>li{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.in{
  position: relative;
  left: 0.2rem;
  display: inline-block;
  background: #59A2ED;
  width: 0.4rem;
  text-align: center;
  padding: 0.08rem 0.1rem;
  color: white;
}
</style>



